import React from "react";
export default function AlertPanel({ alerts }) {
  if (!alerts || alerts.length === 0) return <div className="text-sm text-slate-400">No alerts yet.</div>;
  return (
    <div className="space-y-2 max-h-[60vh] overflow-auto pr-1">
      {alerts.map(a => (
        <div key={a.id} className="bg-slate-800/70 border border-slate-700 rounded-lg p-3">
          <div className="flex items-center gap-2 text-xs text-slate-400">
            <span>{a.symbol}</span><span>•</span><time>{new Date(a.ts).toLocaleString()}</time>
          </div>
          <div className="mt-1 text-sm">{a.message}</div>
        </div>
      ))}
    </div>
  );
}
