import React, { useEffect, useRef, useState } from "react";
import { createChart, CrosshairMode } from "lightweight-charts";
import { RSI as rsiCalc } from "technicalindicators";

const FIBS = [-1.0, -0.618, -0.272, 0.236, 0.382, 0.5, 0.618, 0.786, 1.272, 1.618, 2.0];
const BINANCE_INTERVAL = { "1m": "1m", "5m": "5m", "1h": "1h", "1d": "1d" };
const LOOKBACK = 500;
const SNAP_WINDOW = 200;

const BINANCE_REST = "https://api.binance.us/api";
const BINANCE_WS   = "wss://stream.binance.us:9443/ws/";

function uid() { return Math.random().toString(36).slice(2); }

function toHeikinAshi(bars) {
  if (!bars || !bars.length) return [];
  const out = [];
  for (let i = 0; i < bars.length; i++) {
    const b = bars[i];
    if (i === 0) {
      const o = (b.open + b.close) / 2;
      const c = (b.open + b.high + b.low + b.close) / 4;
      out.push({ time: b.time, open: o, high: Math.max(b.high, o, c), low: Math.min(b.low, o, c), close: c, volume: b.volume });
    } else {
      const prev = out[i - 1];
      const o = (prev.open + prev.close) / 2;
      const c = (b.open + b.high + b.low + b.close) / 4;
      out.push({ time: b.time, open: o, high: Math.max(b.high, o, c), low: Math.min(b.low, o, c), close: c, volume: b.volume });
    }
  }
  return out;
}

export default function AssetChart({ symbol, timeframe, fibLines, onFibLinesUpdate, onAlert, useHeikinAshi = false, useHaRsi = false }) {
  const containerRef = useRef(null);
  const chartRef = useRef(null);
  const seriesRef = useRef(null);
  const tooltipRef = useRef(null);
  const snapLayerRef = useRef(null);

  const [candles, setCandles] = useState([]);
  const [haCandles, setHaCandles] = useState([]);
  const [lastPrice, setLastPrice] = useState(null);
  const [rsiVal, setRsiVal] = useState(null);
  const [haRsiVal, setHaRsiVal] = useState(null);
  const draggingRef = useRef({ active: false, lineId: null, offset: 0 });

  const priceLineMap = useRef({});
  const manualZoomRef = useRef(0);
  function markManualZoom() { manualZoomRef.current = Date.now(); }
  function allowAutoCenter() { return (Date.now() - manualZoomRef.current) > 3000; }
  const autoscaleSeriesRef = useRef(null);
  const wsRef = useRef(null);
  const lastAlertRef = useRef({});

  async function fetchCandles(sym, tf, limit) {
    const url = `${BINANCE_REST}/v3/klines?symbol=${encodeURIComponent(sym)}&interval=${encodeURIComponent(BINANCE_INTERVAL[tf])}&limit=${encodeURIComponent(limit)}`;
    const res = await fetch(url);
    const arr = await res.json();
    return arr.map(k => ({ time: Math.floor(k[0]/1000), open:+k[1], high:+k[2], low:+k[3], close:+k[4], volume:+k[5] }));
  }

  function computeRSI(c) {
    if (!c || c.length < 15) return null;
    const closes = c.map(x => x.close);
    const result = rsiCalc.calculate({ period: 14, values: closes });
    return result && result.length ? result[result.length - 1] : null;
  }

  useEffect(() => {
    if (!containerRef.current) return;
    const el = containerRef.current;
    const chart = createChart(el, {
      layout: { background: { color: "#0f172a" }, textColor: "#e2e8f0" },
      rightPriceScale: { borderColor: "rgba(148,163,184,0.25)" },
      timeScale: { borderColor: "rgba(148,163,184,0.25)" },
      grid: { horzLines: { color: "rgba(30,41,59,0.6)" }, vertLines: { color: "rgba(30,41,59,0.6)" } },
      crosshair: { mode: CrosshairMode.Normal }
    });
    const series = chart.addCandlestickSeries({
      upColor: "#10b981", downColor: "#ef4444",
      borderUpColor: "#10b981", borderDownColor: "#ef4444",
      wickUpColor: "#10b981", wickDownColor: "#ef4444"
    });
    chartRef.current = chart;
    seriesRef.current = series;

    const aux = chart.addLineSeries({ color: "rgba(0,0,0,0)", lineWidth: 0 });
    autoscaleSeriesRef.current = aux;

    const tip = document.createElement("div");
    tip.className = "chart-tooltip hidden";
    el.appendChild(tip);
    tooltipRef.current = tip;

    const layer = document.createElement("div");
    layer.style.position = "absolute"; layer.style.inset = "0"; layer.style.pointerEvents = "none";
    el.appendChild(layer);
    snapLayerRef.current = layer;

    const ro = new ResizeObserver(() => chart.applyOptions({ width: el.clientWidth, height: Math.max(280, el.clientHeight) }));
        function onWheel(){ markManualZoom(); }
        function onMouseDown(e){ try{ const rect = el.getBoundingClientRect(); if (e.clientX > rect.right - 56) markManualZoom(); }catch(_){} }
        el.addEventListener('wheel', onWheel, { passive: true });
        el.addEventListener('mousedown', onMouseDown);
    ro.observe(el);
    return () => {
      ro.disconnect();
      chart.remove();
      chartRef.current = null;
      seriesRef.current = null;
      autoscaleSeriesRef.current = null;
      tooltipRef.current = null;
      snapLayerRef.current = null;
    };
  }, []);

  useEffect(() => {
    let cancelled = false;
    async function loadAndStream() {
      try {
        const data = await fetchCandles(symbol, timeframe, LOOKBACK);
        if (cancelled) return;
        setCandles(data);
        const display = useHeikinAshi ? toHeikinAshi(data) : data;
        setHaCandles(display);
        seriesRef.current?.setData(display);
        chartRef.current?.timeScale().fitContent();
        setRsiVal(computeRSI(data));
        setHaRsiVal(useHeikinAshi ? computeRSI(display) : null);

        const stream = `${symbol.toLowerCase()}@kline_${BINANCE_INTERVAL[timeframe]}`;
        const ws = new WebSocket(`${BINANCE_WS}${stream}`);
        wsRef.current = ws;
        ws.onmessage = (evt) => {
          try {
            const msg = JSON.parse(evt.data);
            const k = msg?.k; if (!k) return;
            const bar = { time: Math.floor(k.t/1000), open:+k.o, high:+k.h, low:+k.l, close:+k.c, volume:+k.v };
            setLastPrice(bar.close);
            setCandles(prev => {
              if (!prev.length || bar.time > prev[prev.length - 1].time) {
                const next = [...prev, bar];
                const dispBar = useHeikinAshi ? toHeikinAshi(next).slice(-1)[0] : bar;
                if (useHeikinAshi) setHaCandles(p => [...p, dispBar]);
                seriesRef.current?.update(dispBar);
                setRsiVal(computeRSI(next));
                if (useHeikinAshi) setHaRsiVal(computeRSI(useHeikinAshi ? toHeikinAshi(next) : next));
                return next;
              } else {
                const next = prev.slice(0, -1).concat(bar);
                const dispBar2 = useHeikinAshi ? toHeikinAshi(next).slice(-1)[0] : bar;
                if (useHeikinAshi) setHaCandles(p => p.slice(0, -1).concat(dispBar2));
                seriesRef.current?.update(dispBar2);
                setRsiVal(computeRSI(next));
                if (useHeikinAshi) setHaRsiVal(computeRSI(useHeikinAshi ? toHeikinAshi(next) : next));
                return next;
              }
            });
          } catch {}
        };
        ws.onerror = () => {
          ws.close();
          wsRef.current = null;
        };
      } catch (e) { console.error("load error", e); }
    }
    loadAndStream();
    return () => { cancelled = false; try { wsRef.current?.close(); } catch {} wsRef.current = null; };
  }, [symbol, timeframe]); // eslint-disable-line

  useEffect(() => {
    if (!seriesRef.current) return;
    const display = useHeikinAshi ? toHeikinAshi(candles) : candles;
    setHaCandles(display);
    seriesRef.current.setData(display);
    setHaRsiVal(useHeikinAshi ? computeRSI(display) : null);
    chartRef.current?.timeScale().fitContent();
  }, [useHeikinAshi]); // eslint-disable-line

  function maybeReanchorFromSwing(data, force = false) {
    if (!fibLines || !fibLines.length) return;
    const src = data && data.length ? (useHeikinAshi ? toHeikinAshi(data) : data) : (useHeikinAshi ? haCandles : candles);
    const slice = src.slice(-SNAP_WINDOW);
    if (!slice.length) return;
    const swingH = Math.max(...slice.map(x => x.high));
    const swingL = Math.min(...slice.map(x => x.low));
    const range = swingH - swingL || 1e-9;
    const need = fibLines.some(f => force ? (f.autoAnchor || f.price == null || f._reanchorNow) : f._reanchorNow);
    if (!need) { centerOnFibPrices(fibLines); return; }
    const next = fibLines.map(f => {
      if (!(f.autoAnchor || f._reanchorNow || (force && (f.price == null)))) return f;
      return { ...f, price: swingL + f.ratio * range, _reanchorNow: undefined };
    });
    onFibLinesUpdate(symbol, next);
    centerOnFibPrices(next);
  }

  function centerOnFibPrices(lines = fibLines) {
        if (!allowAutoCenter()) return;
    if (!autoscaleSeriesRef.current || !lines) return;
    const act = lines.filter(l => l.enabled && l.price != null);
    if (!act.length) return;
    const minP = Math.min(...act.map(l => l.price));
    const maxP = Math.max(...act.map(l => l.price));
    const disp = useHeikinAshi ? haCandles : candles;
    const lastT = (disp[disp.length - 1]?.time) || Math.floor(Date.now()/1000);
    autoscaleSeriesRef.current.setData([
      { time: lastT - 60, value: minP },
      { time: lastT,       value: maxP },
    ]);
  }

  useEffect(() => {
    if (!seriesRef.current || !fibLines) return;
    for (let ln of fibLines) {
      const exist = priceLineMap.current[ln.id];
      if (!ln.enabled) { if (exist) { seriesRef.current.removePriceLine(exist); delete priceLineMap.current[ln.id]; } continue; }
      const opts = { price: ln.price ?? (lastPrice || 0), color: "rgba(147,197,253,0.75)", lineWidth: 1, lineStyle: 0, axisLabelVisible: true, title: "" };
      if (!exist) priceLineMap.current[ln.id] = seriesRef.current.createPriceLine(opts); else exist.applyOptions(opts);
    }
    Object.keys(priceLineMap.current).forEach(id => {
      const still = fibLines.find(l => l.id === id && l.enabled);
      if (!still && seriesRef.current) { seriesRef.current.removePriceLine(priceLineMap.current[id]); delete priceLineMap.current[id]; }
    });
  }, [fibLines, lastPrice]); // eslint-disable-line

  useEffect(() => { centerOnFibPrices(); }, [fibLines]); // eslint-disable-line
  useEffect(() => { maybeReanchorFromSwing(); }, [fibLines]); // eslint-disable-line

  function computeSnapPrice(price) {
    const disp = useHeikinAshi ? haCandles : candles;
    if (!disp.length) return price;
    const slice = disp.slice(-SNAP_WINDOW);
    const swingH = Math.max(...slice.map(x => x.high));
    const swingL = Math.min(...slice.map(x => x.low));
    const range = swingH - swingL;
    const candidates = [...FIBS.map(r => swingL + r * range)];
    disp.slice(-50).forEach(c => { candidates.push(c.high, c.low); });
    let best = price, bestDist = Infinity;
    for (let v of candidates) { const d = Math.abs(v - price); if (d < bestDist) { best = v; bestDist = d; } }
    return best;
  }

  function findLineById(id) { return (fibLines || []).find(ln => ln.id === id) || null; }
  function nudgeLineToPrice(lineId, newPrice, doSnap) {
    if (!fibLines) return;
    const updated = fibLines.map(ln => ln.id === lineId ? { ...ln, price: doSnap ? computeSnapPrice(newPrice) : newPrice } : ln);
    onFibLinesUpdate(symbol, updated);
    centerOnFibPrices(updated);
  }
  function onClickStartDrag(param) {
    if (!seriesRef.current || !fibLines || !param?.point) return;
    const y = param.point.y;
    const series = seriesRef.current;
    const clickedPrice = series.coordinateToPrice(y);
    if (clickedPrice == null) return;
    const proxPx = 6;
    let target = null;
    for (let ln of fibLines) {
      if (!ln.enabled || ln.price == null) continue;
      const py = series.priceToCoordinate(ln.price);
      if (py == null) continue;
      if (Math.abs(py - y) <= proxPx) { target = ln; break; }
    }
    if (!target) return;
    const c = containerRef.current;
    const state = draggingRef.current = { active: true, lineId: target.id, offset: target.price - clickedPrice };
    function onMove(ev) {
      if (!state.active) return;
      const rect = c.getBoundingClientRect();
      const yNow = ev.clientY - rect.top;
      const pxToPrice = series.coordinateToPrice(yNow);
      if (pxToPrice == null) return;
      nudgeLineToPrice(state.lineId, pxToPrice + state.offset, false);
    }
    function onUp() {
      if (!state.active) return;
      const current = findLineById(state.lineId);
      if (current) nudgeLineToPrice(current.id, computeSnapPrice(current.price), true);
      state.active = false; state.lineId = null; state.offset = 0;
      window.removeEventListener("mousemove", onMove);
      window.removeEventListener("mouseup", onUp);
    }
    window.addEventListener("mousemove", onMove);
    window.addEventListener("mouseup", onUp);
  }

  useEffect(() => {
    function onKey(e) {
      if (!fibLines?.length) return;
      const base = lastPrice ?? 0;
      let target = null;
      for (let ln of fibLines) {
        if (!ln.enabled || ln.price == null) continue;
        if (!target || Math.abs(ln.price - base) < Math.abs(target.price - base)) target = ln;
      }
      if (!target) return;
      const fast = e.shiftKey ? 5 : (e.ctrlKey || e.metaKey ? 0.2 : 1);
      const tick = (base || 1) * 0.0005 * fast;
      if (e.key === "ArrowUp") nudgeLineToPrice(target.id, target.price + tick, true);
      else if (e.key === "ArrowDown") nudgeLineToPrice(target.id, Math.max(0, target.price - tick), true);
    }
    window.addEventListener("keydown", onKey);
    return () => window.removeEventListener("keydown", onKey);
  }, [fibLines, lastPrice]); // eslint-disable-line

  useEffect(() => {
    if (!fibLines || lastPrice == null) return;
    const selRsi = (useHeikinAshi && useHaRsi) ? haRsiVal : rsiVal;
    for (let ln of fibLines) {
      if (!ln.enabled || !ln.alertEnabled || ln.price == null) continue;
      const priceCross =
        (ln.crossDir === "both" && Math.abs(lastPrice - ln.price) / Math.max(1, ln.price) < 0.00005) ||
        (ln.crossDir === "above" && lastPrice >= ln.price) ||
        (ln.crossDir === "below" && lastPrice <= ln.price);
      const rsiPass = (selRsi != null) && (ln.rsiOp === ">=" ? selRsi >= ln.rsiThreshold : selRsi <= ln.rsiThreshold);
      if (priceCross && rsiPass) {
        const payload = { id: uid(), symbol, ts: Date.now(), message: `Alert ${ln.label || ("Fib " + ln.ratio)} at ${ln.price.toFixed(6)} | RSI ${selRsi?.toFixed(2)} (${ln.rsiOp} ${ln.rsiThreshold})` };
        onAlert?.(payload);
        fetch("/api/send-telegram", { method: "POST", headers: { "Content-Type": "application/json" }, body: JSON.stringify({ text: `${symbol}: ${payload.message}` }) }).catch(() => {});
      }
    }
  }, [lastPrice, rsiVal, haRsiVal, fibLines, useHeikinAshi, useHaRsi]); // eslint-disable-line

  useEffect(() => {
    if (!chartRef.current || !seriesRef.current || !tooltipRef.current) return;
    const chart = chartRef.current, series = seriesRef.current, tip = tooltipRef.current;
    function clearSnap() { const layer = snapLayerRef.current; if (!layer) return; while (layer.firstChild) layer.removeChild(layer.firstChild); }
    function drawSnap(nearest) {
      const layer = snapLayerRef.current; if (!layer || !series) return;
      clearSnap();
      (fibLines || []).forEach(ln => {
        if (!ln.enabled || ln.price == null) return;
        const y = series.priceToCoordinate(ln.price); if (y == null) return;
        const div = document.createElement("div"); div.className = "snap-line"; if (nearest && nearest.id === ln.id) div.classList.add("highlight"); div.style.top = y + "px"; layer.appendChild(div);
      });
    }
    function onMove(param) {
      if (!param?.point) { tip.classList.add("hidden"); clearSnap(); return; }
      const p = param.point;
      const price = series.coordinateToPrice(p.y);
      if (price == null) { tip.classList.add("hidden"); clearSnap(); return; }
      let nearest = null;
      (fibLines || []).forEach(ln => {
        if (!ln.enabled || ln.price == null) return;
        const dist = Math.abs(ln.price - price);
        if (!nearest || dist < nearest.dist) nearest = { id: ln.id, price: ln.price, ratio: ln.ratio, dist };
      });
      tip.classList.remove("hidden");
      tip.style.left = p.x + "px"; tip.style.top = p.y + "px";
      tip.innerHTML = nearest ? `<div class='font-mono'>${price.toFixed(6)}</div><div class='text-xs text-slate-400'>near Fib ${nearest.ratio} @ ${nearest.price.toFixed(6)}</div>` : `<div class='font-mono'>${price.toFixed(6)}</div>`;
      drawSnap(nearest);
    }
    function onLeave() { tip.classList.add("hidden"); clearSnap(); }
    chart.subscribeCrosshairMove(onMove);
    containerRef.current.addEventListener("mouseleave", onLeave);
    chart.subscribeClick(onClickStartDrag);
    return () => { chart.unsubscribeCrosshairMove(onMove); chart.unsubscribeClick(onClickStartDrag); containerRef.current?.removeEventListener("mouseleave", onLeave); };
  }, [fibLines]); // eslint-disable-line

  return (
    <div>
      <div className="flex flex-wrap items-center gap-3 mb-3">
        <div className="flex items-center gap-2 text-sm">
          <span className="text-slate-400">RSI (raw):</span>
          <span className="font-mono mr-4">{rsiVal == null ? "-" : rsiVal.toFixed(2)}</span>
          <span className="text-slate-400">RSI (HA):</span>
          <span className="font-mono">{haRsiVal == null ? "-" : haRsiVal.toFixed(2)}</span>
        </div>
      </div>
      <div ref={containerRef} className="relative w-full h-[420px] rounded-xl border border-slate-800 bg-slate-900 shadow-soft" />
    </div>
  );
}
